/* Lightbox */

.lightbox-gallery {
    background: $color-masthead;
    img {
        opacity: 1;
        transition: opacity 0.25s ease;
    }
    img:hover {        
        cursor: pointer;
        opacity: 0.85;
    }
}

.lightbox-view {    
    position: relative;    
}

.lightbox-button {    
    background: $color-masthead;
    color: $color-white !important;
    position: absolute;
    text-transform: uppercase;
    padding: $space;
    bottom: $space;
    left: $space;
    z-index: 1;   

    &:hover,
    &:focus {
        text-decoration: none;
        background: $color-masthead-light;
    }
}